@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html, body {
    height: 100%;
    margin: 0;
    background-color: white;
}

#root, #app-wrapper {
    height: 100%;
}

#app-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Lato', sans-serif;
}

.sidebar-wrapper {
    display: flex;
    height: 100%;
}

.sidebar {
    width: 275px;
    background-color: #1f292e;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
}

.sidebar-settings {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 20px
}

.sidebar-setting {
    font-size: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.sidebar-setting > * {
    margin: 5px 0;
}

.sidebar-button {
    padding: 3px;
    font-size: 15px;
}

.sidebar-button {
    background-color: #33435d;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-weight: 600;
}

.sidebar-button:hover {
    color: #98d6e8;
    cursor: pointer;
}

.main-sidebar {
    display: flex;
    flex: 1 1;
}

.documentation-wrapper {
    display: flex;
    justify-content: center;
    flex: 1 1;
}

.markdown-wrapper {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
}

.markdown {
    display: flex;
    justify-content: center;
    flex-direction: column;
    word-wrap: break-word;
    margin: 30px;
    font-size: 18px;
    width: 50vw;
}

.markdown > p {
    padding: 10px 0;
}

.markdown code {
    font-family: "Titillium Web", sans-serif;
    font-size: 0.875rem;
    font-weight: bold;
}

.markdown ul {
    margin-left: 20px;
}

.markdown li {
    padding: 3px 0;
}

.markdown a {
    text-decoration: none;
    color: #58a6ff
}

.markdown h2 {
    padding: 10px 0;
}

.sections-wrapper {
    background-color: #dce2ef;
    padding: 20px;
    border-radius: 4px;
}

.sections-header {
    font-size: 20px;
}

.sections ul {
    margin: 0;
}

.sections ul li {
    list-style-type: none;
    padding: 10px;
    margin: 10px 0;
    font-size: 15px;
    border-radius: 10px;
    cursor: pointer;
    color: black;
}


.sections ul li:hover {
    background-color: #cfd6ef;
}

.section-break {
    margin: 0 13px;
    height: 1px;
    background-color: #c5c5c5;
    border: none;
}

.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
* {
    margin: 0;
    padding: 0;
}

#header {
    background-color: #33435d;
    display: flex;
    align-items: center;
    color: white;
    word-break: break-all;
}

#logo-wrapper {
    display: flex;
    align-items: center;
    margin-right: 50px;
}

#logo-wrapper a {
    color: white;
    text-decoration: none;
}

#name {
    font-size: 40px;
    padding: 10px 10px;
    color: white;
}

#name:hover {
    color: #98d6e8;
    transition: all 150ms linear;
}

#nav-bar > ul {
    padding: 0;
    margin: 0;
}

#nav-bar > ul > li {
    display: inline-block;
    font-size: 30px;
}

.nav-link {
    text-decoration: none;
    color: white;
    cursor: pointer;
    width: 100%;
}

.nav-option:hover, .algos-option:hover {
    color: #98d6e8;
    transition: all 150ms linear;
}


.nav-option {
    display: flex;
    align-items: center;
    padding: 15px 30px 16px 0;
    word-break: break-all;
}

.fa-caret-down {
    margin-top: 5px;
    margin-left: 3px;
}

#dropdown {
    position: absolute;
    top: 85px;
    background-color: #475c83;
    border-radius: 5px;
    display: none;
    -webkit-animation: growDown 350ms ease-in-out forwards;
            animation: growDown 350ms ease-in-out forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    z-index: 1;
}

#dropdown ul li {
    font-size: 20px;
    color: white;
    display: flex;
}

.algos-option {
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@-webkit-keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0)
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1)
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1)
    }
}

@keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0)
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1)
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1)
    }
}

#footer {
    background-color: #33435d;
    margin-top: auto;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 8px 0;
    word-break: break-all;
}

#footer-header {
    font-size: 20px;
    margin: 0;
}

#contact-wrapper {
    width: 100%;
}

#contact-wrapper ul {
    display: flex;
    justify-content: center;
    align-items: center;
}

#contact-wrapper ul li {
    display: flex;
    align-items: center;
    flex-direction: row;
    padding: 5px;
    font-size: 15px;
}

.social-link {
    text-decoration: none;
    color: white;
}

.social-link:hover {
    color: #98d6e8;
    transition: all 150ms linear;
    cursor: pointer;
}

.social-text {
    padding: 0 15px 0 3px;
}
#home-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex: 1 1;
}

#home-header-text {
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    font-size: 50px;
    margin-top: 20px;
    text-align: center;
}

#home-text {
    text-align: center;
    font-size: 20px;
    padding: 10px;
    width: 38vw;
}

#buttons-menu {
    display: flex;
}

#buttons-menu a {
    text-decoration: none;
}

.home-section {
    background-color: #33435d;
    color: white;
    padding: 10px;
    margin: 10px 5px 20px 5px;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;

}

.home-section:hover {
    color: #98d6e8;
    transition: all 150ms linear;
}
#documentation-wrapper {
    display: flex;
    align-items: center;
    flex: 1 1;
}

.sidebar {
    display: flex;
    justify-content: flex-start;
}

#documentation-section {
    width: 100%;
    text-align: center;
}

.section {
    font-size: 20px;
    padding: 20px;
}

.section:hover {
    background-color: #3e5072 !important;
    color: #98d6e8;
    transition: all 150ms linear;
}

.documentation-link {
    text-decoration: none;
    color: white;
}



#bars-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: auto;
    margin: 50px 0;
    flex: 1 1;
}

#bars {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 100%;
}

.array-bar {
    display: flex;
    width: 3px;
    margin: 0 1px;
}

.current-option {
    overflow: hidden;
    text-align: center;
}

.selection-dropdown {
    display: flex;
    justify-content: space-between;
    background-color: #33435d;
    padding: 0 5px;
    cursor: pointer;
}

.selection-options {
    display: none;
    position: relative;
    margin: 0;
    -webkit-animation: growDown 250ms ease-in-out forwards;
            animation: growDown 250ms ease-in-out forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
}

.selection-options ul {
    position: absolute;
    left: 0;
    right: 0;
    top: -5px;
    bottom: -67px;
    overflow-y: scroll;
    background-color: #33435d;
}

/* width */
.selection-options ul::-webkit-scrollbar {
    width: 10px;
}

/* Handle */
.selection-options ul::-webkit-scrollbar-thumb {
    background: #fff;
}

.selection-options ul div {
    cursor: pointer;
}

.selection-options ul div:hover{
    background-color: #3e5072;
    transition: all 150ms linear;
}

